import React, { Component } from "react";
import CarouselComponent2 from "./carouselComponent2";
import Col from "react-bootstrap/Col";
import Row from "react-bootstrap/Row";
import stbc from './images/STBIC.png';
import fcmb from './images/FCMB.png';
import cardimg from './images/cardimg.png';
import visa from './images/icon/visa.png';
import mcard from './images/icon/mcard.png';
import paypal from './images/icon/paypal.png';
import ipay from './images/icon/apple-pay.png';


class PayTax extends Component {
  render() {
    return (
        <div style={{ backgroundColor: '#F2F4F8' }}>

            < CarouselComponent2 />



            <div>
                <Row className='container-fluid mt-3 mb-3' xs={1} md={2} lg={2}>
                    <Col>
                        <div className="container ms-4 ps-5 mt-4">
                            <strong><h4 style={{ fontWeight: '700', fontSize: '30px' }}>MAKE PAYMENT ONLINE</h4></strong>
                            <p style={{ textAlign: 'left' }}>
                                <p>Kindly enter the following Card Details to Make payment Online</p>
                                <p><strong>ATM Card Number:</strong> The 16 digits number in your ATM Card</p>
                                <p><strong>Expiry Date:</strong> The Expiry date of your ATM Card</p>
                                <p><strong>CVV:</strong> The 3 digits number at the reverse side of your card</p>
                                <p><strong>NOTE: Pay the accurate Amount for your Tax </strong></p>
                            </p>
                            <div className="">
                                <strong><h4 style={{ fontWeight: '600', fontSize: '25px', marginLeft: '80px' }}>OUR PARTNERS</h4></strong>
                                <img src={visa} alt='logo' height={70} />
                                <img src={mcard} alt='logo' height={70} style={{ paddingLeft: '20px' }} />
                                <img src={paypal} alt='logo' height={70} style={{ paddingLeft: '20px' }} />
                                <img src={ipay} alt='logo' height={70} style={{ paddingLeft: '20px' }} />
                            </div>
                        </div>
                    </Col>
                    <Col>
                        <div className="wrap mt-4" style={{ padding: '5px' }}>
                            <p> <img src={cardimg} alt='payLogo' style={{ maxWidth: '600px', maxHeight: '300px' }} /></p>
                        </div>
                    </Col>
                </Row>
                <Row className='container-fluid mt-3 mb-3' xs={1} md={2} lg={2}>
                    <Col>
                        <div className="container ms-4 ps-5 mt-4">
                            <strong><h4 style={{ fontWeight: '700', fontSize: '30px' }}>ZENITH BANK USSD CODE</h4></strong>
                            <strong><p>How to perform bank transactions with USSD codes</p></strong>
                            <p style={{ textAlign: 'left' }}>
                                <p>First, type in your banks USSD code into your phone (registered with the bank) and press call button
                                    Follow the prompt as displayed on your phone.</p>
                                <p>OR Simply dial *966*320*Phone No.*Amount# from your registered Zenith Bank SIM</p>
                            </p>
                        </div>
                    </Col>
                    <Col className="ps-5">
                        <p> <img src={stbc} alt='stbc' height={100} style={{ marginLeft:'190px'  }} />
                        <strong> <p  style={{ marginLeft:'100px'}}> Pay with ZENITH BANK USSD Codes.<br />
                            *966*320*Phone No.*Amount#</p></strong>
                        </p>
                        
                    </Col>
                </Row>
                <hr />
                <Row className='container-fluid mt-3 mb-3' xs={1} md={2} lg={2}>
                    <Col>
                        <div className="container ms-4 ps-5 mt-4">

                            <strong><h4 style={{ fontWeight: '700', fontSize: '30px' }}>ADDITIONAL PAYMENT METHOD</h4></strong>
                            <strong><p>Proceed to any FCMB Branch to Make Payment</p></strong>
                            <p style={{ textAlign: 'left' }}>
                                <p>Kindly walk into any FCMB Branch closer to you to make physical payment into the DelTax Account.</p>
                            </p>
                        </div>
                    </Col>
                    <Col>

                        <div className="container ms-4 ps-5" style={{ padding: '5px', }}>
                            <p> <img src={fcmb} alt='fcmb' height={100}  style={{ marginLeft:'150px' }}/></p>
                            <strong><p  style={{ marginLeft:'80px'}}>Visit any FCMB branch to make PAYMENT <br />
                                ACCOUNT NUMBER: 2115884856 <br />
                                ACCOUNT NAME: Deltax
                            </p></strong>
                        </div>
                    </Col>
                </Row>

            </div>
        </div>
        );
    }
}
export default PayTax;