import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
// import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import {LinkContainer} from 'react-router-bootstrap'
// import logo from './logo.jpg';
// import SubmissionForm from './submissionComponent';
import insta from './images/icon/instagram.png';
import fbook from './images/icon/facebook.png';
import twitter from './images/icon/twitter.png';
import youtube from './images/icon/youtube.png';
import mail from './images/icon/gmail.png';
import phone from './images/icon/phone-call.png';
import location from './images/icon/placeholder.png';
import about from './images/icon/about.png';
import pay from './images/icon/pay.png';
import status from './images/icon/status.png';
import policy from './images/icon/privacy.png';
import termc from './images/icon/termc.png';



const FooterComponent = () => {
    return ( 
        <footer className='bg-none p-2' style={{backgroundColor:'#0055FF'}}>
            <Row className='container-fluid mt-1 mb-1'>
            <Col xs={12} md={4} lg={3}>
                <div className='p-4'>
                   <h3 className="text-light-head">Navigation</h3>
                   <LinkContainer to="/payTax">
            <Nav.Link className='text-light'><img src={pay} style={{maxWidth:'7%'}} alt='Company Logo' /> Pay Your Tax</Nav.Link>
            </LinkContainer>
            <LinkContainer to="/aboutUs">
            <Nav.Link className='text-light'><img src={about} style={{maxWidth:'7%'}} alt='Company Logo' /> About Us</Nav.Link>
            </LinkContainer>
            <LinkContainer to="#">
            <Nav.Link className='text-light'><img src={status} style={{maxWidth:'7%'}} alt='Company Logo' /> Check Status</Nav.Link>
            </LinkContainer>
                </div>
            </Col>
            <Col xs={12} md={4} lg={3}>
            <div className='p-4'>
                   <h3 className="text-light-head">Contact Us</h3>
                   <LinkContainer to="#">
            <Nav.Link className='text-light mb-2'><img src={mail} style={{maxWidth:'7%'}} alt='Company Logo' /> Support@deltax.com</Nav.Link>
            </LinkContainer>
            <LinkContainer to="#">
            <Nav.Link className='text-light mb-2'><img src={phone} style={{maxWidth:'7%'}} alt='Company Logo' /> +2347061879831</Nav.Link>
            </LinkContainer>
            <LinkContainer to="#">
            <Nav.Link className='text-light'><img src={location} style={{maxWidth:'7%'}} alt='Company Logo'/> 19 Sokoba Avenue, Asaba, Delta State Nigeria</Nav.Link>
            </LinkContainer>
                </div>
            </Col>
            <Col xs={12} md={4} lg={3}>
            <div className='p-4'>
                   <h3 className="text-light-head">Follow Us</h3>
                   <LinkContainer to="#">
            <Nav.Link className='text-light p-1'><img src={fbook} style={{maxWidth:'7%'}} alt='Company Logo' /> Facebook</Nav.Link>
            </LinkContainer>
            <LinkContainer to="#">
            <Nav.Link className='text-light p-1'><img src={insta} style={{maxWidth:'7%'}} alt='Company Logo'/> Instagram</Nav.Link>
            </LinkContainer>
            <LinkContainer to="#">
            <Nav.Link className='text-light p-1'><img src={twitter} style={{maxWidth:'7%'}} alt='Company Logo' /> Twitter</Nav.Link>
            </LinkContainer>
            <LinkContainer to="#">
            <Nav.Link className='text-light p-1'><img src={youtube} style={{maxWidth:'7%'}} alt='Company Logo' /> Youtube</Nav.Link>
            </LinkContainer>
                </div>
            </Col>
            <Col xs={12} md={4} lg={3}>
            <div className='p-4'>
                   <h3 className="text-light-head">Terms and Policies</h3>
                   <LinkContainer to="#">
            <Nav.Link className='text-light p-1'><img src={termc} style={{maxWidth:'7%'}} alt='Company Logo' /> Terms</Nav.Link>
            </LinkContainer>
            <LinkContainer to="#">
            <Nav.Link className='text-light mb-2 p-1'><img src={policy} style={{maxWidth:'7%'}} alt='Company Logo' /> Policies</Nav.Link>
            </LinkContainer>
                </div>
            </Col>
                <p style={{color:'#ffffff', fontSize:'14px', fontWeight:'500', textAlign:'center'}}>&copy; 2022 deltax.com</p>
            </Row>
            
         </footer>
     );
}
 
export default FooterComponent;