import Button from 'react-bootstrap/Button';
import Card from 'react-bootstrap/Card';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import { Link } from 'react-router-dom';
import CarouselComponent2 from './carouselComponent2';

const BodyDetailsComponent = () => {
    return ( 
       
        <div style={{backgroundColor:'#F2F4F8'}}>
        <CarouselComponent2 />
            <Row className='container-fluid' style={{backgroundColor:'#F2F4F8'}}>
                <Col className='p-5'>
                    {
                        <Card style={{backgroundColor:'#F2F4F8', border:"none"}} >
                            <Card.Body as="h5" style={{ fontSize: '40px', textAlign: 'center', fontWeight:'800' }}>PAY YOUR TAX TODAY</Card.Body>
                            
                            <Card.Body>
                                <Card.Text>
                            {/* <Link to="/">
                                    <Button variant="primary">Go Back Home</Button>
                                </Link> */}
                            
                                    <p className='p-4' style={{ fontSize: '18px', margin: '15px', textAlign: 'justify' }}>
                                    <Card.Title><strong style={{ fontSize: '24px',fontWeight:'600' }}>WHAT IS TAX?</strong></Card.Title>                                    
                                    Taxes are mandatory contributions levied on individuals or corporations by a government entitywhether local, state, or federal. Tax revenues finance government activities, including public works and services such as roads and schools, or programs such as Social Security and Medicare.
                                    Citizens can be taxed directly or indirectly.
                                    A direct tax is a tax that a person or organization pays directly to the entity that imposed it. Examples include income tax, real property tax, personal property tax, and taxes on assets, all of which are paid by an individual taxpayer directly to the government.
                                    Unlike direct taxes, indirect taxes are levied on goods and services, not individual payers, and collected by the retailer or manufacturer. Sales and Value-Added Taxes (VATs) are two examples of indirect taxes.
                                    </p>
                                    <p className='p-4' style={{ fontSize: '18px', margin: '15px', textAlign: 'justify' }}>
                                    <Card.Title><strong style={{ fontSize: '24px',fontWeight:'600' }}>WHAT IS DONE WITH YOUR TAX?</strong></Card.Title>
                                    To help fund public works and servicesand to build and maintain the infrastructure used in a countrya government usually taxes its individual and corporate residents. The tax collected is used for the betterment of the economy and all who are living in it. The society can better function and useful works can be done. Public utilities such as water, energy, and waste management systems
                                    Public utilities such as water, energy, and waste management systems
                                    Tax is levied on a wide range of income stemming from salary, profits from business, property rental, etc can be readily provided by the governemnt. The state government have set up agency and department for the handling of tax revenue which are adequately managed for the right purpose. Such agency include the Delta state Internal Revenue Service
                                                </p>
                                    <p className='p-4' style={{ fontSize: '18px', margin: '15px', textAlign: 'justify' }}>
                                    <Card.Title><strong style={{ fontSize: '24px',fontWeight:'600' }}>POSSIBLE REWARDS FOR PAYING YOUR TAX?</strong></Card.Title>
                                    The purpose of taxes is to provide the government with funds for spending without inflation, and to improve the lifestyle of its citizen. Taxes are used by the government for a variety of purposes, some of which are:<br/>
                                    <ul>
                                    <li>Funding of public infrastructure</li>
                                    <li>Development and welfare projects</li>
                                    <li>Defense expenditure</li>
                                    <li>Scientific research</li>
                                    <li>Public insurance</li>
                                    <li>Salaries of state and government employees</li>
                                    <li>Operation of the government</li>
                                    <li>Public transportation</li>
                                    <li>Unemployment benefits</li>
                                    <li>Pension schemes</li>
                                    <li>Law enforcement</li>
                                    <li>Public health</li>
                                    <li> Public education</li>
                                    <li>Public utilities</li>
                                    </ul>
                                    It is compulsory and beneficial for anyone who earns a taxable salary (which is one that exceeds the basic exemption limit) to file their income tax returns. This is the case even if the tax liability is zero after deductions. However, even if your income is less than the basic exemption limit, there are advantages to filing taxes
                                    </p>
                                    <p className='p-4' style={{ fontSize: '18px', margin: '15px', textAlign: 'justify' }}>
                                    <Card.Title><strong style={{ fontSize: '24px',fontWeight:'600' }}>POSSIBLE PENALTIES FOR NOT PAYING YOUR TAX?</strong></Card.Title>
                                    A tax is a compulsory financial charge or some other type of levy imposed on a taxpayer (an individual or legal entity) by a governmental organization in order to fund government spending and various public expenditures (regional, local, or national), and tax compliance refers to policy actions and individual behaviour aimed at ensuring that taxpayers are paying the right amount of tax at the right time and securing the correct tax allowances and tax reliefs. <strong>A failure to pay in a timely manner (non-compliance), along with evasion of or resistance to taxation, is punishable by law.</strong><br/> Taxes consist of direct or indirect taxes and may be paid in money or as its labour equivalent. Tax is levied on a wide range of income stemming from salary, profits from business, property rental, etc. There are also wealth taxes, sales taxes, property taxes, payroll taxes, value-added taxes, service taxes, etc.
                                    </p>






                                    <p style={{textAlign: 'center'}}>
                                    <Link to="/payTax">
                                    <Button variant="danger">PAY YOUR TAX</Button>
                                </Link>
                                </p>
                                </Card.Text>
                            </Card.Body>
                        </Card>
                    }
                </Col>
            </Row>
        </div>
     );
}
 
export default BodyDetailsComponent;