import React, { Component } from "react";
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import background from './images/bg.jpg';
import {LinkContainer} from 'react-router-bootstrap'
import { Link } from 'react-router-dom';
import Nav from 'react-bootstrap/Nav';

class Login extends Component {
  render() {
    return (
      <div
        style={{
        backgroundImage: `url(${background})`,
          height: '78vh',
          marginTop: '10px',
          textAlign: "center"
        }}
      >
     <Form className="wrap">
     <strong><h2>Welcome Back!</h2></strong>
        <h6>Please Enter Your Details to Continue</h6>
        <div className="mb-3">
        <strong><label>Username OR Mobile Number</label></strong>
          <input required
            type="number"
            className="form-control"
            placeholder="Enter Mobile Number"
            style={{border:'2px solid #0055FF'}}
          />
        </div>
        <div className="mb-3">
         <strong> <label>Password</label></strong>
          <input required
            type="password"
            className="form-control"
            placeholder="Enter password"
            style={{border:'2px solid #0055FF'}}
          />
        </div>
        <div className="mb-3">
          <div className="custom-control custom-checkbox">
            <input  
              type="checkbox"
              className="custom-control-input"
              id="customCheck1"
              />
           <label className="custom-control-label" htmlFor="customCheck1">
              Remember me
            </label> 
          </div>
        </div>
        <div className="d-grid">
        <Link to="/paytax">
            <Button variant="primary" style={{backgroundColor:'#0055FF', width:'300px'}}>Login</Button>
        </Link>
        </div>
       <strong> <p className="forgot-password text-center">
          Forgot <a href="/">password?</a></p>
        </strong>
        <strong>
        <p className="forgot-password text-center">
        Dont have an Account Yet?
        <LinkContainer to="/register">
         <Nav.Link className="forgot-password text-primary text-center"> Register</Nav.Link>
        </LinkContainer></p>
        </strong>
      </Form>
      </div>
    );
  }
}
export default Login;