import Button from 'react-bootstrap/Button';

function ValidButtons() {
  return (
    <div className='inline-flex items-center justify-center p-2 rounded-md'>
      <Button variant="danger ms-0 me-0" size='lg' style={{backgroundColor:'#FF0000' }} >INVALID</Button>{' '}
      <Button variant="primary ps-5 pe-5" style={{backgroundColor:'#0055FF'}}>MOTOCYCLE (OKADA)</Button>{' '}
      <Button variant="primary" style={{backgroundColor:'#0055FF'}}>WAR-579AS</Button>{' '}

    </div>
  );
}

export default ValidButtons;