import Button from 'react-bootstrap/Button';
import Card from 'react-bootstrap/Card';
import CardGroup from 'react-bootstrap/CardGroup';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import { Link } from 'react-router-dom';
import CarouselComponent from './carouselComponent';
import abt1 from './images/okowa.jpg';
import abt2 from './images/TaxDir.jpg';

const AboutUs = () => {
    return ( 
       
        <div style={{backgroundColor:'#F2F4F8'}}>
        <CarouselComponent />
            <Row className='container-fluid' style={{backgroundColor:'#F2F4F8'}}>
                <Col className='p-5'>
                    {
                        <Card style={{backgroundColor:'#F2F4F8', border:"none"}}>
                            <Card.Body as="h5" style={{ fontSize: '40px', textAlign: 'center', fontWeight:'800' }}>ABOUT US</Card.Body>
                            
                            <Card.Body>
                                <Card.Text>
                            {/* <Link to="/">
                                    <Button variant="primary">Go Back Home</Button>
                                </Link>
                             */}
                                    <p className='p-4' style={{ fontSize: '18px', margin: '15px', textAlign: 'justify', fontWeight:'400'}}>
                                    <Card.Title><strong style={{ fontSize: '24px',fontWeight:'600'}}>DELTAX</strong></Card.Title>
                                        The DelTax System improves the regulation of registered drivers in the state and also provides the government with fully accounting of tax revenue which is used to sustain Delta State. Taxation aids a county or state to grow in all aspects.

                                        <Card.Title className='mb-1 mt-5'><strong style={{ fontSize: '24px',fontWeight:'600'}}>WHAT WE DO</strong></Card.Title>
                                        Taxation system been the major funding avenue of the Delta State Government, DelTax has been saddled with the responsibility of collecting taxes and other revenues. 
                                        Over the years, Delta State has expenrienced downturn in tax refund! DelTax is set to increase the States Internally Generated Revenue (IGR) with the newly executed pioneering program (DelTax) and implement strategies that will positively impact revenue generation and collection, thereby making Delta State less dependent on proceeds from Federation Account / Federal Allocation. 

                                        As Delta State move towards meeting the needs of a mega state, DelTax will continue to position itself as a major funding arm of the state government by putting in place improved structures that will ensure collection of the proper amount of revenue at the least cost.

                                        <Card.Title className='mb-1 mt-5'><strong style={{ fontSize: '24px',fontWeight:'600'}}>OUR VISION AND MISSION</strong></Card.Title>
                                        To motivate trust in the governments taxation system by ecncouraging the payment of taxes, which will further boost economic growth and infrastructure development while improving the standard of living in towns and communities in delta state. 

                                        <Card.Title className='mb-1 mt-5'><strong style={{ fontSize: '24px',fontWeight:'600'}}>REGIONS</strong></Card.Title>
                                        <p>Asaba Office - 13, Okarobi Avenue, Asaba</p>
                                        <p>Kwale Office - 190, Ifeanyi Street, Kwale</p>
                                        <p>Ughelli Office - 102, Elohor Road, Ughelli</p>
                                        Warri Office - 5, Okarobi Avenue, Asaba
                                        <Card.Title className='mb-1 mt-5'><strong style={{ fontSize: '24px',fontWeight:'600'}}>EXECUTIVE BOARD</strong></Card.Title>
                                        The Delta State Board of Internal Revenue (DBIR) is an extra-Ministerial Department that was established at the creation of Delta State on the 27th August, 1991. It is an offshoot of the former Bendel State Board of Internal Revenue from which Delta State was created. 
                                        The core function of DBIR is revenue generation through the assessment and collection of taxes/tariffs to enable the State Government to fund its numerous financial commitments.
                                        With their vision to always assist the Delta State Government with the required funds for the development of the state. And mission to use every reasonable legal and conventional means to bring eligible Tax payers into the tax net to meet the vision of the Board.
                                    </p>

                                        
<center><CardGroup style={{ width: '34rem', textAlign: 'center'}} >
      <Card className='p-1 m-3'>
        <Card.Img src={abt1} />
        <Card.Body>
          <Card.Title>Sen. Dr Ifeanyi A. Okowa</Card.Title>
          <Card.Text>
          Governor of Delta State
          </Card.Text>
        </Card.Body>
      </Card>
      <Card className='p-1 m-3'>
        <Card.Img src={abt2}/>
        <Card.Body>
          <Card.Title>Prof. Tee Slim Austa</Card.Title>
          <Card.Text>
            Director of DELTAX
          </Card.Text>
        </Card.Body>
      </Card>
    </CardGroup>
    </center>

                                    <p style={{textAlign: 'center'}}>
                                      <p strong style={{ fontSize: '36px',fontWeight:'600', color:'#1A1A1A'}}>
                                        Don't Know <br/>
                                        Where to Begin?
                                      </p>
                                    <Link to="/contactUs">
                                    <Button className='btn-light ps-5 pe-5' style={{border:'3px solid #0055FF', color:'#0055FF', fontSize:'18px', fontWeight:'600'}}>Contact Our Help Desk</Button>
                                </Link>
                                </p>
                                </Card.Text>
                            </Card.Body>
                        </Card>
                    }
                </Col>
            </Row>
        </div>
     );
}
 
export default AboutUs;