import NavbarComponent from './navbarComponent';
import HomeComponent from './home';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom'
import FooterComponent from './footerComponent';
import Error404 from './error404';
import BodyDetailsComponent from './bodyDetailsComponent';
import AboutUs from './aboutUs';
import Login from './loginComponent';
import Register from './registerComponent';
import RegisterValidate from './registerValidateComponet';
import RegisterSuccess from './registerSuccessComponent';
import PayTax from './payTaxComponent';
import ContactUs from './contactUs';


function App() {
  return (
    <div className="App">
      <Router>
        <header>
          < NavbarComponent />
        </header>
        <Switch>

          <Route exact path="/deltax">
            < HomeComponent />
          </Route>
        
          <Route exact path="/payTax">
            <PayTax />
          </Route>

          <Route exact path="/aboutUs">
            <AboutUs/>
          </Route>

          <Route exact path="/contactUs">
          <ContactUs />
          </Route>

          <Route exact path="/bodyDetails">
            <BodyDetailsComponent />
          </Route>

          <Route exact path="/login">
            <Login/>
          </Route>

          <Route exact path="/register">
            <Register />
          </Route>

          <Route exact path="/registerValidate">
            <RegisterValidate />
          </Route>

          <Route exact path="/registerSuccess">
            <RegisterSuccess />
          </Route>

          <Route  path="*">
            <Error404/>
          </Route>

        </Switch>
        < FooterComponent />
      </Router>
    </div>
  );
}

export default App;
