import Carousel from "react-bootstrap/Carousel";
import slider1 from './images/slider21.jpg'
import slider2 from './images/slider22.jpg'
import slider3 from './images/slider23.jpg'


const CarouselComponent2 = () => {
    return (  
        <Carousel className="sliderHeight">
      <Carousel.Item className="sliderHeight">
        <img
          className="d-block w-100"
          src={slider1}
          alt="First slide"
        />
        <Carousel.Caption>
        <h3 className="sliderText" style={{fontSize:'48px', fontWeight:'600'}}>GOOD WORKS ARE DONE WITH YOUR TAX!</h3>
        <strong><p className="sliderText"></p></strong>
        </Carousel.Caption>
      </Carousel.Item>
      <Carousel.Item className="sliderHeight">
        <img
          className="d-block w-100"
          src={slider2}
          alt="Second slide"
        />

        <Carousel.Caption>
        <h3 className="sliderText" style={{fontSize:'48px', fontWeight:'600'}}>GOOD WORKS ARE DONE WITH YOUR TAX!</h3>
        <strong><p className="sliderText"></p></strong>
        </Carousel.Caption>
      </Carousel.Item>
      <Carousel.Item className="sliderHeight">
        <img
          className="d-block w-100"
          src={slider3}
          alt="Third slide"
        />

        <Carousel.Caption>
        <h3 className="sliderText" style={{fontSize:'48px', fontWeight:'600'}}>GOOD WORKS ARE DONE WITH YOUR TAX!</h3>
        <strong> <p className="sliderText">
          </p></strong>
        </Carousel.Caption>
      </Carousel.Item>
    </Carousel>
    );
}
 
export default CarouselComponent2;