import React, { Component } from "react";
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import background from './images/bg.jpg';
import {LinkContainer} from 'react-router-bootstrap'
import Nav from 'react-bootstrap/Nav';
import { Link } from 'react-router-dom';


class RegisterValidate extends Component {
  render() {
    return (
      <div
        style={{
        backgroundImage: `url(${background})`,
          height: '55vh',
          marginTop: '10px',
          textAlign: "center"
        }}
      >
     <Form className="wrap">
     <strong><h6>Good Works Are Done with your Tax</h6></strong>
        <h3>Complete Registration!</h3>
        <div className="mb-3 mt-3">
        <strong><label>Please Enter Registration Code</label></strong>
          <input required
            type="text"
            className="form-control"
            placeholder="Enter The Registration Code Sent to your Phone"
            style={{border:'2px solid #0055FF'}}
          />
        </div>      
        <div className="d-grid">
        <Link to="/registerSuccess">
        <Button variant="primary" style={{backgroundColor:'#0055FF', paddingLeft:'80px', paddingRight:'80px'}}>REGISTER</Button>
        </Link>
        </div>
        <strong>
        <p className="forgot-password text-center">
        Didn't receive Code...?
        <LinkContainer to="/registerValidate">
         <Nav.Link className="forgot-password text-primary text-center">Resend Code</Nav.Link>
        </LinkContainer></p>
        </strong>
      </Form>
      </div>
    );
  }
}
export default RegisterValidate;