import Navbar from 'react-bootstrap/Navbar';
import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import logo from './logo.png';
import Image from 'react-bootstrap/Image';
import {LinkContainer} from 'react-router-bootstrap'

const NavbarComponent = () => {
    const stylesNav={
               font: 'Poppins , sans-serif',    
            }
    return ( 
        <div>
            <Navbar bg="none " expand="lg" text="primary" style={{backgroundColor:'#F2F4F8'}}>
      <Container className='ontainer-fluid' fluid>
        <LinkContainer to="/deltax">
        <Navbar.Brand>
          <Image src={logo} className="navHead ps-4"/>
        </Navbar.Brand>
        </LinkContainer>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="ms-auto pe-5" style={stylesNav}>
          <LinkContainer to="/deltax">
            <Nav.Link className='text-primary'>HOME</Nav.Link>
            </LinkContainer>
          <LinkContainer to="payTax">
            <Nav.Link className='text-primary'>PAY YOUR TAX</Nav.Link>
            </LinkContainer>
            <LinkContainer to="/aboutUs">
            <Nav.Link className='text-primary head'>ABOUT US</Nav.Link>
            </LinkContainer>
            <LinkContainer to="/contactUs">
            <Nav.Link className='text-primary head'>CONTACT US</Nav.Link>
            </LinkContainer>
            <LinkContainer to="/login">
            <Nav.Link className='btn btn-outline-primary ps-4 pe-4'>LOGIN</Nav.Link>
            </LinkContainer>
            <LinkContainer to="/register">
            <Nav.Link className='btn btn-primary'><span className='LinkText ps-2 pe-2' style={{color:'white'}}>REGISTER</span></Nav.Link>
            </LinkContainer>
          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar>
        </div>
     );
}
 
export default NavbarComponent;