import Image404 from './images/404.png'
import Button from 'react-bootstrap/Button';
import Card from 'react-bootstrap/Card';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import {Link} from 'react-router-dom';

const Error404 = () => {
    return (   
        <Row style={{backgroundColor:'#F2F4F8'}}>
        <Col className='p-5'>
             <Card className='text-center'>
            <Card.Header as="h5" style={{fontSize: '30px', fontWeight:'600'}}>Page Not Found!</Card.Header>
            <Card.Body>
              <Card.Title><small></small></Card.Title>
              <Card.Text>
                <img src={Image404} className="image-fluid p-0" alt="404" width={400} height={300}/> 
                <p style={{fontSize: '32px', fontWeight:'700'}}>Oops! this page is unknown or does not exist</p>
                <p style={{fontSize: '16px', fontWeight:'400'}}>Sorry about that, but the page you are looking for isn't available.</p>
              </Card.Text>
              <Link to="/deltax">
              <Button variant="primary">Go Back Home</Button>
              </Link>
            </Card.Body>
          </Card>
          </Col>
          </Row> 
     );
}
 
export default Error404;